/* Skills.css */
.skills-section {
    padding: 4em 2em;
    background-color: #f4f4f4;
    text-align: center;
}

.section-title {
    font-size: 2.5em;
    margin-bottom: 1em;
    color: #333;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 2em;
    margin-top: 2em;
    justify-items: center;
}

.skill-card {
    background-color: #fff;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-align: center;
}

.skill-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skill-icon {
    font-size: 3em;
    color: #0066cc;
    margin-bottom: 10px;
    transition: color 0.3s ease;
}

.skill-card p {
    font-size: 1.2em;
    color: #333;
    font-weight: bold;
}

.skill-card:hover .skill-icon {
    color: #004080;
}

/* Responsive Design */
@media (max-width: 768px) {
    .skills-grid {
        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    }

    .skill-card p {
        font-size: 1em;
    }
}
